import React, { useState } from 'react';
import Modal from 'react-modal';
import '../ressource/style/experiences.css';
import profil from '../image/profil.JPG';
import accueil from '../image/accueil.PNG';
import sectDirect from '../image/sectDirect.PNG';
import picking from '../image/picking.PNG';
import { Link } from 'react-router-dom';

Modal.setAppElement('#root'); // Ncessaire pour l'accessibilit

const Experience = () => {
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [selectedProject, setSelectedProject] = useState(null);

    const openModal = (project) => {
        setSelectedProject(project);
        console.log(project);
        setModalIsOpen(true);
    };

    const closeModal = () => {
        setSelectedProject(null);
        setModalIsOpen(false);
    };

    const projet = [
        {
            id: 1,
            titre: 'ISDset',
            image: accueil,
            description: 'Une plateforme de gestion pour un institut suprieur.',
            Technologies: 'ReactJS, Node.js, MySQL',
            Fonctionnalits: 'Inscription en ligne, gestion des tudiants et des cours, dashboard admin et tudiant.',
            github: "https://github.com/AnniePulcherie/projet-isdset",
        },
        {
            id: 2,
            titre: "Picking List",
            image: picking,
            description: "Une application web permettant de vrifier des articles en scannant leur code-barres",
            Technologies: "PHP, Laravel, Javascript, MYSQL",
            Fonctionnalits: "Vrification d'articles par code-barres, Interface utilisateur pour la recherche et la vrification des articles, Gestion des articles et des inventaires",
            github: "https://github.com/AnniePulcherie/Picking_list/tree/master",
        },
        {
            id: 3,
            titre: "Gestion de File d'Attente avec Notifications Instantanes",
            image: sectDirect,
            description: "Une application web permettant de grer les rendez-vous et la file d'attente des visiteurs en temps rel grce  des notifications instantanes.",
            Technologies: "Nodejs, Express, Reactjs, MySQL, Socket.io, Json Web Token",
            Fonctionnalits: "Organisation et suivi des visiteurs en temps rel, Envoi de notifications pour informer les utilisateurs des mises  jour en temps rel, Utilisation de JWT pour grer les sessions utilisateur de manire scurise",
            Dfis : "Communication en temps rel : Mise en place de Socket.io pour une communication bidirectionnelle en temps rel; Gestion des sessions : Implmentation de JWT pour scuriser et grer les sessions utilisateur; Synchronisation des donnes : Assurer la cohrence et la synchronisation des donnes de la file d'attente entre le frontend et le backend.",
            github: "https://github.com/AnniePulcherie/filAttenteFrontEndReactJs/tree/master",
        },
    ];

    return (
        <div className='liste-style'>
            {projet && projet.map(pro => (
                <div key={pro.id} className='card'>
                    <div className='card-image'>
                        <img src={pro.image} alt='img2'/>
                        <div>
                            <p>{pro.titre}</p>
                        </div>
                        <p>{pro.description}</p>
                        <button onClick={() => openModal(pro)}>Afficher les dtails</button>
                    </div>
                </div>
            ))}

            {selectedProject && (
                <Modal
                    isOpen={modalIsOpen}
                    onRequestClose={closeModal}
                    contentLabel="Dtails du Projet"
                    className="Modal"
                    
                >
                     <div className="modal-content">
                        <img src={selectedProject.image} alt='sary' />
                        <div className="modal-text">
                            <h2>{selectedProject.titre}</h2>
                            <p><strong>Description :</strong> {selectedProject.description}</p>
                            <p><strong>Technologies :</strong> {selectedProject.Technologies}</p>
                            <p><strong>Fonctionnalits :</strong> {selectedProject.Fonctionnalits}</p>
                            {selectedProject.Dfis && <p><strong>Dfis :</strong> {selectedProject.Dfis}</p>}
                        </div>
                    </div>
                    <div className="modal-actions">
                        <button onClick={closeModal}>Fermer</button>
                        <Link to={selectedProject.github} target="_blank">GitHub</Link>
                    </div>
                </Modal>
            )}
        </div>
    );
};

export default Experience;
